import React from 'react';
import { View, Text, StyleSheet, Image } from 'react-native';
import { Ionicons } from '@expo/vector-icons';
import { useTheme } from 'react-native-paper';

const TimeBasedHeader = ({ timeOfDay, userName }) => {
  const { colors } = useTheme();
  
  const getGreeting = () => {
    switch (timeOfDay) {
      case 'morning':
        return ' ';
      case 'afternoon':
        return '  ';
      case 'evening':
        return '  ';
      default:
        return '';
    }
  };

  const getSubText = () => {
    switch (timeOfDay) {
      case 'morning':
        return '    ';
      case 'afternoon':
        return '    ';
      case 'evening':
        return '   ';
      default:
        return '  ';
    }
  };

  const getBackgroundColor = () => {
    switch (timeOfDay) {
      case 'morning':
        return '#FFF8E6';
      case 'afternoon':
        return '#F0F8FF';
      case 'evening':
        return '#F5F0FF';
      default:
        return '#F9F5FF';
    }
  };

  const getHeaderIcon = () => {
    switch (timeOfDay) {
      case 'morning':
        return 'sunny-outline';
      case 'afternoon':
        return 'cafe-outline';
      case 'evening':
        return 'moon-outline';
      default:
        return 'star-outline';
    }
  };

  return (
    <View style={[styles.container, { backgroundColor: getBackgroundColor() }]}>
      <View style={styles.content}>
        <View style={styles.textContainer}>
          <Text style={styles.greeting}>
            {getGreeting()}, <Text style={{ color: colors.primary }}>{userName}</Text>
          </Text>
          <Text style={styles.subText}>{getSubText()}</Text>
        </View>
        <View style={styles.iconContainer}>
          <Ionicons name={getHeaderIcon()} size={36} color={colors.primary} />
        </View>
      </View>
      
      <View style={styles.statsContainer}>
        <View style={styles.statItem}>
          <Text style={styles.statNumber}>2</Text>
          <Text style={styles.statLabel}> </Text>
        </View>
        <View style={styles.divider} />
        <View style={styles.statItem}>
          <Text style={styles.statNumber}>5</Text>
          <Text style={styles.statLabel}> </Text>
        </View>
        <View style={styles.divider} />
        <View style={styles.statItem}>
          <Text style={styles.statNumber}>150</Text>
          <Text style={styles.statLabel}> </Text>
        </View>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    paddingTop: 50,
    paddingBottom: 20,
    paddingHorizontal: 16,
    marginBottom: 20,
  },
  content: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  textContainer: {
    flex: 1,
  },
  greeting: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 4,
  },
  subText: {
    fontSize: 16,
    color: '#666',
  },
  iconContainer: {
    width: 50,
    height: 50,
    borderRadius: 25,
    backgroundColor: 'rgba(255, 255, 255, 0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  statsContainer: {
    flexDirection: 'row',
    backgroundColor: 'white',
    borderRadius: 12,
    marginTop: 20,
    padding: 16,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 1 },
    shadowOpacity: 0.1,
    shadowRadius: 2,
    elevation: 2,
  },
  statItem: {
    flex: 1,
    alignItems: 'center',
  },
  statNumber: {
    fontSize: 20,
    fontWeight: 'bold',
  },
  statLabel: {
    fontSize: 12,
    color: '#666',
    marginTop: 4,
  },
  divider: {
    width: 1,
    height: '100%',
    backgroundColor: '#E0E0E0',
  },
});

export default TimeBasedHeader; 