//     
const getLevelUpRequirement = (level) => {
  switch(level) {
    case 1: return " 5   3";
    case 2: return " 15   10";
    case 3: return " 30   20";
    case 4: return " 50   30";
    default: return "";
  }
};

//   ()  -      
const getLevelProgress = (level) => {
  //   
  switch(level) {
    case 1: return 60;
    case 2: return 45;
    case 3: return 30;
    case 4: return 20;
    default: return 0;
  }
};

