import React, { useState, useContext } from 'react';
import { 
  View, 
  Text, 
  StyleSheet, 
  TouchableOpacity, 
  ScrollView, 
  KeyboardAvoidingView, 
  Platform 
} from 'react-native';
import { TextInput, Button, useTheme, RadioButton } from 'react-native-paper';
import { useForm, Controller } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Ionicons } from '@expo/vector-icons';
import AuthContext from '../../context/AuthContext';

//  
const schema = yup.object().shape({
  email: yup
    .string()
    .required(' ')
    .email('  '),
  password: yup
    .string()
    .required(' ')
    .min(8, '  8  ')
    .matches(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]/,
      ' , , ,   '
    ),
  confirmPassword: yup
    .string()
    .required('  ')
    .oneOf([yup.ref('password'), null], '  '),
  name: yup
    .string()
    .required(' ')
    .min(2, '  2  '),
  nickname: yup
    .string()
    .required(' ')
    .min(2, '  2  ')
    .max(15, '  15 '),
});

const SignUpScreen = ({ navigation }) => {
  const { colors } = useTheme();
  const { signUp } = useContext(AuthContext);
  const [secureTextEntry, setSecureTextEntry] = useState(true);
  const [confirmSecureTextEntry, setConfirmSecureTextEntry] = useState(true);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [userType, setUserType] = useState(' ');

  const { control, handleSubmit, formState: { errors } } = useForm({
    resolver: yupResolver(schema),
    defaultValues: {
      email: '',
      password: '',
      confirmPassword: '',
      name: '',
      nickname: '',
    },
  });

  const toggleSecureEntry = () => {
    setSecureTextEntry(!secureTextEntry);
  };

  const toggleConfirmSecureEntry = () => {
    setConfirmSecureTextEntry(!confirmSecureTextEntry);
  };

  const onSubmit = async (data) => {
    setLoading(true);
    setError('');
    
    try {
      //    API 
      setTimeout(() => {
        //   
        const userData = {
          token: 'test_token_' + Math.random().toString(36).substring(2),
          user: {
            id: '1',
            email: data.email,
            name: data.name,
            nickname: data.nickname,
            userType: userType,
            level: 1,
            points: {
              total: 50,
              available: 50
            }
          }
        };
        
        signUp(userData);
        setLoading(false);
      }, 1500);
    } catch (err) {
      setError(' .  .');
      setLoading(false);
    }
  };

  return (
    <KeyboardAvoidingView
      behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContainer}>
        <View style={styles.header}>
          <TouchableOpacity 
            onPress={() => navigation.goBack()} 
            style={styles.backButton}
          >
            <Ionicons name="arrow-back" size={24} color="black" />
          </TouchableOpacity>
          <Text style={styles.title}></Text>
          <View style={styles.placeholder} />
        </View>

        <View style={styles.formContainer}>
          {error ? <Text style={styles.errorText}>{error}</Text> : null}

          <Controller
            control={control}
            name="email"
            render={({ field: { onChange, value, onBlur } }) => (
              <TextInput
                label=""
                value={value}
                onChangeText={onChange}
                onBlur={onBlur}
                mode="outlined"
                left={<TextInput.Icon icon="email-outline" />}
                error={!!errors.email}
                style={styles.input}
                autoCapitalize="none"
                keyboardType="email-address"
              />
            )}
          />
          {errors.email && <Text style={styles.errorText}>{errors.email.message}</Text>}

          <Controller
            control={control}
            name="password"
            render={({ field: { onChange, value, onBlur } }) => (
              <TextInput
                label=""
                value={value}
                onChangeText={onChange}
                onBlur={onBlur}
                mode="outlined"
                secureTextEntry={secureTextEntry}
                left={<TextInput.Icon icon="lock-outline" />}
                right={
                  <TextInput.Icon 
                    icon={secureTextEntry ? "eye-off" : "eye"} 
                    onPress={toggleSecureEntry} 
                  />
                }
                error={!!errors.password}
                style={styles.input}
              />
            )}
          />
          {errors.password && <Text style={styles.errorText}>{errors.password.message}</Text>}

          <Controller
            control={control}
            name="confirmPassword"
            render={({ field: { onChange, value, onBlur } }) => (
              <TextInput
                label=" "
                value={value}
                onChangeText={onChange}
                onBlur={onBlur}
                mode="outlined"
                secureTextEntry={confirmSecureTextEntry}
                left={<TextInput.Icon icon="lock-check-outline" />}
                right={
                  <TextInput.Icon 
                    icon={confirmSecureTextEntry ? "eye-off" : "eye"} 
                    onPress={toggleConfirmSecureEntry} 
                  />
                }
                error={!!errors.confirmPassword}
                style={styles.input}
              />
            )}
          />
          {errors.confirmPassword && <Text style={styles.errorText}>{errors.confirmPassword.message}</Text>}

          <Controller
            control={control}
            name="name"
            render={({ field: { onChange, value, onBlur } }) => (
              <TextInput
                label=""
                value={value}
                onChangeText={onChange}
                onBlur={onBlur}
                mode="outlined"
                left={<TextInput.Icon icon="account-outline" />}
                error={!!errors.name}
                style={styles.input}
              />
            )}
          />
          {errors.name && <Text style={styles.errorText}>{errors.name.message}</Text>}

          <Controller
            control={control}
            name="nickname"
            render={({ field: { onChange, value, onBlur } }) => (
              <TextInput
                label=""
                value={value}
                onChangeText={onChange}
                onBlur={onBlur}
                mode="outlined"
                left={<TextInput.Icon icon="account-badge-outline" />}
                error={!!errors.nickname}
                style={styles.input}
              />
            )}
          />
          {errors.nickname && <Text style={styles.errorText}>{errors.nickname.message}</Text>}

          <View style={styles.userTypeContainer}>
            <Text style={styles.userTypeLabel}> :</Text>
            <RadioButton.Group
              onValueChange={setUserType}
              value={userType}
            >
              <View style={styles.radioButtonContainer}>
                <RadioButton value=" " />
                <Text> </Text>
              </View>
              <View style={styles.radioButtonContainer}>
                <RadioButton value=" " />
                <Text> </Text>
              </View>
              <View style={styles.radioButtonContainer}>
                <RadioButton value=" " />
                <Text> </Text>
              </View>
            </RadioButton.Group>
            <Text style={styles.userTypeDescription}>
              {userType === ' ' ? 
                '     .' : 
                userType === ' ' ? 
                '     .' : 
                '      .'}
            </Text>
          </View>

          <View style={styles.termsContainer}>
            <Text style={styles.termsText}>
                <Text style={{ color: colors.primary }}></Text>, <Text style={{ color: colors.primary }}> </Text>  .
            </Text>
          </View>

          <Button 
            mode="contained" 
            onPress={handleSubmit(onSubmit)} 
            style={styles.button}
            loading={loading}
            disabled={loading}
          >
            
          </Button>
        </View>

        <View style={styles.footer}>
          <Text style={styles.footerText}>  ?</Text>
          <TouchableOpacity onPress={() => navigation.navigate('SignIn')}>
            <Text style={[styles.signInText, { color: colors.primary }]}></Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
    </KeyboardAvoidingView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: 'white',
  },
  scrollContainer: {
    flexGrow: 1,
    paddingHorizontal: 24,
    paddingTop: 40,
    paddingBottom: 24,
  },
  header: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    marginBottom: 24,
  },
  backButton: {
    padding: 8,
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
  },
  placeholder: {
    width: 40,
  },
  formContainer: {
    marginBottom: 24,
  },
  input: {
    marginBottom: 12,
  },
  userTypeContainer: {
    marginVertical: 16,
  },
  userTypeLabel: {
    fontSize: 16,
    fontWeight: 'bold',
    marginBottom: 8,
  },
  radioButtonContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    marginVertical: 4,
  },
  userTypeDescription: {
    fontSize: 14,
    color: '#666',
    fontStyle: 'italic',
    marginTop: 8,
  },
  termsContainer: {
    marginVertical: 16,
  },
  termsText: {
    fontSize: 14,
    color: '#666',
    textAlign: 'center',
  },
  button: {
    paddingVertical: 8,
    marginBottom: 16,
  },
  errorText: {
    color: '#BA1A1A',
    fontSize: 12,
    marginBottom: 8,
  },
  footer: {
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  footerText: {
    color: '#666',
    marginRight: 8,
  },
  signInText: {
    fontWeight: 'bold',
    fontSize: 16,
  },
});

export default SignUpScreen; 