import React, { useState, useContext } from 'react';
import { 
  View, 
  Text, 
  StyleSheet, 
  TouchableOpacity, 
  Image, 
  KeyboardAvoidingView, 
  Platform,
  ScrollView
} from 'react-native';
import { TextInput, Button, useTheme } from 'react-native-paper';
import { Ionicons } from '@expo/vector-icons';
import { useForm, Controller } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import AuthContext from '../../context/AuthContext';

//  
const schema = yup.object().shape({
  email: yup
    .string()
    .required(' ')
    .email('  '),
  password: yup
    .string()
    .required(' ')
    .min(8, '  8  '),
});

const SignInScreen = ({ navigation }) => {
  const { colors } = useTheme();
  const { signIn } = useContext(AuthContext);
  const [secureTextEntry, setSecureTextEntry] = useState(true);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const { control, handleSubmit, formState: { errors } } = useForm({
    resolver: yupResolver(schema),
    defaultValues: {
      email: '',
      password: '',
    },
  });

  const toggleSecureEntry = () => {
    setSecureTextEntry(!secureTextEntry);
  };

  const onSubmit = async (data) => {
    setLoading(true);
    setError('');
    
    try {
      //    API 
      setTimeout(() => {
        //   
        const userData = {
          token: 'test_token_' + Math.random().toString(36).substring(2),
          user: {
            id: '1',
            email: data.email,
            name: ' ',
            nickname: '',
            level: 2,
            points: {
              total: 250,
              available: 150
            }
          }
        };
        
        signIn(userData);
        setLoading(false);
      }, 1500);
    } catch (err) {
      setError(' .  .');
      setLoading(false);
    }
  };

  return (
    <KeyboardAvoidingView
      behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
      style={styles.container}
    >
      <ScrollView contentContainerStyle={styles.scrollContainer}>
        <View style={styles.logoContainer}>
          {/* <Image 
            source={require('../../assets/logo.png')} 
            style={styles.logo}
            //   (   )
            defaultSource={require('../../assets/logo-placeholder.png')}
          /> */}
          <Text style={[styles.logoText, {fontSize: 40, marginBottom: 10}]}></Text>
          <Text style={styles.logoText}></Text>
          <Text style={styles.tagline}>   </Text>
        </View>

        <View style={styles.formContainer}>
          <Text style={styles.title}></Text>
          
          {error ? <Text style={styles.errorText}>{error}</Text> : null}

          <Controller
            control={control}
            name="email"
            render={({ field: { onChange, value, onBlur } }) => (
              <TextInput
                label=""
                value={value}
                onChangeText={onChange}
                onBlur={onBlur}
                mode="outlined"
                left={<TextInput.Icon icon="email-outline" />}
                error={!!errors.email}
                style={styles.input}
                autoCapitalize="none"
                keyboardType="email-address"
              />
            )}
          />
          {errors.email && <Text style={styles.errorText}>{errors.email.message}</Text>}

          <Controller
            control={control}
            name="password"
            render={({ field: { onChange, value, onBlur } }) => (
              <TextInput
                label=""
                value={value}
                onChangeText={onChange}
                onBlur={onBlur}
                mode="outlined"
                secureTextEntry={secureTextEntry}
                left={<TextInput.Icon icon="lock-outline" />}
                right={
                  <TextInput.Icon 
                    icon={secureTextEntry ? "eye-off" : "eye"} 
                    onPress={toggleSecureEntry} 
                  />
                }
                error={!!errors.password}
                style={styles.input}
              />
            )}
          />
          {errors.password && <Text style={styles.errorText}>{errors.password.message}</Text>}

          <TouchableOpacity 
            style={styles.forgotPassword}
            onPress={() => navigation.navigate('ForgotPassword')}
          >
            <Text style={[styles.forgotPasswordText, { color: colors.primary }]}>
               ?
            </Text>
          </TouchableOpacity>

          <Button 
            mode="contained" 
            onPress={handleSubmit(onSubmit)} 
            style={styles.button}
            loading={loading}
            disabled={loading}
          >
            
          </Button>

          <View style={styles.divider}>
            <View style={styles.dividerLine} />
            <Text style={styles.dividerText}></Text>
            <View style={styles.dividerLine} />
          </View>

          <View style={styles.socialLoginContainer}>
            <TouchableOpacity style={[styles.socialButton, styles.kakaoButton]}>
              <Text style={styles.socialButtonText}> </Text>
            </TouchableOpacity>
            
            <TouchableOpacity style={[styles.socialButton, styles.naverButton]}>
              <Text style={[styles.socialButtonText, styles.naverButtonText]}> </Text>
            </TouchableOpacity>
            
            <TouchableOpacity style={[styles.socialButton, styles.googleButton]}>
              <Ionicons name="logo-google" size={18} color="#444" />
              <Text style={[styles.socialButtonText, styles.googleButtonText]}>Google </Text>
            </TouchableOpacity>
          </View>
        </View>

        <View style={styles.footer}>
          <Text style={styles.footerText}>  ?</Text>
          <TouchableOpacity onPress={() => navigation.navigate('SignUp')}>
            <Text style={[styles.signUpText, { color: colors.primary }]}></Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
    </KeyboardAvoidingView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: 'white',
  },
  scrollContainer: {
    flexGrow: 1,
    paddingHorizontal: 24,
    paddingTop: 40,
    paddingBottom: 24,
  },
  logoContainer: {
    alignItems: 'center',
    marginBottom: 40,
  },
  logo: {
    width: 80,
    height: 80,
    marginBottom: 12,
    backgroundColor: '#F9F5FF',
    borderRadius: 15,
  },
  logoText: {
    fontSize: 32,
    fontWeight: 'bold',
  },
  tagline: {
    fontSize: 16,
    color: '#666',
    marginTop: 4,
  },
  formContainer: {
    marginBottom: 24,
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 24,
  },
  input: {
    marginBottom: 12,
  },
  forgotPassword: {
    alignSelf: 'flex-end',
    marginBottom: 24,
  },
  forgotPasswordText: {
    fontSize: 14,
  },
  button: {
    paddingVertical: 8,
    marginBottom: 16,
  },
  errorText: {
    color: '#BA1A1A',
    fontSize: 12,
    marginBottom: 8,
  },
  divider: {
    flexDirection: 'row',
    alignItems: 'center',
    marginVertical: 24,
  },
  dividerLine: {
    flex: 1,
    height: 1,
    backgroundColor: '#E0E0E0',
  },
  dividerText: {
    paddingHorizontal: 16,
    color: '#666',
  },
  socialLoginContainer: {
    marginBottom: 24,
  },
  socialButton: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    paddingVertical: 12,
    marginBottom: 12,
    borderRadius: 8,
  },
  kakaoButton: {
    backgroundColor: '#FAE100',
  },
  naverButton: {
    backgroundColor: '#03C75A',
  },
  googleButton: {
    backgroundColor: '#FFFFFF',
    borderWidth: 1,
    borderColor: '#E0E0E0',
  },
  socialButtonText: {
    fontSize: 16,
    fontWeight: 'bold',
  },
  naverButtonText: {
    color: 'white',
  },
  googleButtonText: {
    color: '#444',
    marginLeft: 8,
  },
  footer: {
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  footerText: {
    color: '#666',
    marginRight: 8,
  },
  signUpText: {
    fontWeight: 'bold',
    fontSize: 16,
  },
});

export default SignInScreen; 