import React, { useState, useContext } from 'react';
import { 
  View, 
  Text, 
  StyleSheet, 
  ScrollView, 
  TouchableOpacity, 
  Alert,
  KeyboardAvoidingView,
  Platform,
  ActivityIndicator,
  Image
} from 'react-native';
import { 
  TextInput, 
  Button, 
  Chip, 
  HelperText, 
  Switch, 
  Avatar, 
  Divider,
  Appbar,
  useTheme 
} from 'react-native-paper';
import { Ionicons } from '@expo/vector-icons';
import * as ImagePicker from 'expo-image-picker';
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import AuthContext from '../../context/AuthContext';

//  
const categories = [
  '', '', '', '', '', '',
  '', '', '', '', '', '',
  '', '', '', '', '', '',
  '', '', '', '', '', '',
  ''
];

//   
const schema = yup.object({
  title: yup
    .string()
    .required(' ')
    .min(5, '  5  ')
    .max(100, '  100  '),
  content: yup
    .string()
    .required(' ')
    .min(20, '  20  ')
    .max(2000, '  2000  '),
  selectedCategories: yup
    .array()
    .min(1, ' 1   ')
    .max(3, '  3  '),
  tags: yup
    .string()
    .matches(/^[-a-zA-Z0-9\s,]*$/, '    '),
  points: yup
    .number()
    .transform((value) => (isNaN(value) ? undefined : value))
    .nullable()
    .min(10, ' 10   ')
    .max(10000, ' 10,000   ')
});

const WriteQuestionScreen = ({ navigation }) => {
  const { colors } = useTheme();
  const { user } = useContext(AuthContext);
  const [loading, setLoading] = useState(false);
  const [images, setImages] = useState([]);
  const [isAnonymous, setIsAnonymous] = useState(false);
  const [isTimeLimit, setIsTimeLimit] = useState(false);
  const [timeLimit, setTimeLimit] = useState(24); //  
  const [tagInput, setTagInput] = useState('');
  const [tags, setTags] = useState([]);

  const { control, handleSubmit, formState: { errors }, setValue, watch } = useForm({
    resolver: yupResolver(schema),
    defaultValues: {
      title: '',
      content: '',
      selectedCategories: [],
      tags: '',
      points: 10
    }
  });

  const selectedCategories = watch('selectedCategories');
  const pointsValue = watch('points');

  //   
  const toggleCategory = (category) => {
    const currentCategories = [...selectedCategories];
    const index = currentCategories.indexOf(category);
    
    if (index > -1) {
      currentCategories.splice(index, 1);
    } else {
      if (currentCategories.length < 3) {
        currentCategories.push(category);
      } else {
        Alert.alert('  ', '  3  .');
        return;
      }
    }
    
    setValue('selectedCategories', currentCategories);
  };

  //  
  const pickImage = async () => {
    if (images.length >= 5) {
      Alert.alert('  ', '  5   .');
      return;
    }

    const { status } = await ImagePicker.requestMediaLibraryPermissionsAsync();
    
    if (status !== 'granted') {
      Alert.alert(' ', '   .');
      return;
    }

    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.Images,
      allowsEditing: true,
      aspect: [4, 3],
      quality: 0.8,
    });

    if (!result.canceled) {
      setImages([...images, result.assets[0].uri]);
    }
  };

  //  
  const removeImage = (index) => {
    const newImages = [...images];
    newImages.splice(index, 1);
    setImages(newImages);
  };

  //  
  const addTag = () => {
    if (!tagInput.trim()) return;
    
    //    (,  )
    const formattedTags = tagInput
      .trim()
      .split(/[\s,]+/)
      .filter(tag => tag.length > 0);
    
    if (formattedTags.length === 0) return;
    
    //        
    const newTags = [...tags];
    
    formattedTags.forEach(tag => {
      if (!newTags.includes(tag) && newTags.length < 10) {
        newTags.push(tag);
      }
    });
    
    setTags(newTags);
    setTagInput('');
    setValue('tags', newTags.join(','));
  };

  //  
  const removeTag = (index) => {
    const newTags = [...tags];
    newTags.splice(index, 1);
    setTags(newTags);
    setValue('tags', newTags.join(','));
  };

  //   
  const onSubmit = async (data) => {
    //  
    if (data.points > (user?.points?.available || 0)) {
      Alert.alert(' ', '      .');
      return;
    }

    setLoading(true);

    try {
      //   (    )
      const uploadedImages = images.map(uri => ({
        type: 'image',
        url: uri,
        caption: ''
      }));

      //  
      const processedTags = tags;

      //   
      const questionData = {
        title: data.title,
        content: data.content,
        categories: data.selectedCategories,
        tags: processedTags,
        media: uploadedImages,
        offeredPoints: parseInt(data.points),
        isAnonymous: isAnonymous,
        timeRestriction: {
          hasTimeLimit: isTimeLimit,
          expiresAt: isTimeLimit ? new Date(Date.now() + timeLimit * 60 * 60 * 1000) : null
        }
      };

      //    -   API 
      console.log(' :', questionData);
      
      //   (  )
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      //  
      Alert.alert(
        '  ', 
        '  .\n   .', 
        [{ text: '', onPress: () => navigation.navigate('') }]
      );
    } catch (error) {
      console.error('  :', error);
      Alert.alert(' ', '    .  .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <KeyboardAvoidingView 
      style={{ flex: 1 }} 
      behavior={Platform.OS === 'ios' ? 'padding' : undefined}
      keyboardVerticalOffset={Platform.OS === 'ios' ? 64 : 0}
    >
      <Appbar.Header>
        <Appbar.BackAction onPress={() => navigation.goBack()} />
        <Appbar.Content title=" " />
        <Appbar.Action icon="check" onPress={handleSubmit(onSubmit)} disabled={loading} />
      </Appbar.Header>

      <ScrollView style={styles.container}>
        {loading && (
          <View style={styles.loadingOverlay}>
            <ActivityIndicator size="large" color={colors.primary} />
            <Text style={styles.loadingText}>  ...</Text>
          </View>
        )}

        <View style={styles.formContainer}>
          {/*   */}
          <View style={styles.formGroup}>
            <Text style={styles.label}></Text>
            <Controller
              control={control}
              name="title"
              render={({ field: { onChange, onBlur, value } }) => (
                <TextInput
                  style={styles.input}
                  placeholder="  "
                  value={value}
                  onChangeText={onChange}
                  onBlur={onBlur}
                  error={!!errors.title}
                  maxLength={100}
                />
              )}
            />
            {errors.title && (
              <HelperText type="error">{errors.title.message}</HelperText>
            )}
          </View>

          {/*   */}
          <View style={styles.formGroup}>
            <Text style={styles.label}></Text>
            <Controller
              control={control}
              name="content"
              render={({ field: { onChange, onBlur, value } }) => (
                <TextInput
                  style={styles.contentInput}
                  placeholder="   "
                  value={value}
                  onChangeText={onChange}
                  onBlur={onBlur}
                  error={!!errors.content}
                  multiline={true}
                  numberOfLines={8}
                  maxLength={2000}
                />
              )}
            />
            {errors.content && (
              <HelperText type="error">{errors.content.message}</HelperText>
            )}
          </View>

          {/*   */}
          <View style={styles.formGroup}>
            <Text style={styles.label}>  (,  5)</Text>
            <View style={styles.imagesContainer}>
              <TouchableOpacity style={styles.addImageButton} onPress={pickImage}>
                <Ionicons name="add" size={32} color="#666" />
              </TouchableOpacity>
              
              {images.map((uri, index) => (
                <View key={index} style={styles.imagePreviewContainer}>
                  <TouchableOpacity
                    style={styles.removeImageButton}
                    onPress={() => removeImage(index)}
                  >
                    <Ionicons name="close-circle" size={20} color="white" />
                  </TouchableOpacity>
                  <Image source={{ uri }} style={styles.imagePreview} />
                </View>
              ))}
            </View>
          </View>

          {/*   */}
          <View style={styles.formGroup}>
            <Text style={styles.label}> (1~3 )</Text>
            <View style={styles.categoriesContainer}>
              {categories.map((category, index) => (
                <Chip
                  key={index}
                  style={[
                    styles.categoryChip,
                    selectedCategories.includes(category) && { backgroundColor: colors.primary + '20' }
                  ]}
                  textStyle={selectedCategories.includes(category) ? { color: colors.primary } : {}}
                  onPress={() => toggleCategory(category)}
                  selected={selectedCategories.includes(category)}
                  mode="outlined"
                >
                  {category}
                </Chip>
              ))}
            </View>
            {errors.selectedCategories && (
              <HelperText type="error">{errors.selectedCategories.message}</HelperText>
            )}
          </View>

          {/*   */}
          <View style={styles.formGroup}>
            <Text style={styles.label}> (,  10)</Text>
            <View style={styles.tagInputContainer}>
              <TextInput
                style={styles.tagInput}
                placeholder="    (,   )"
                value={tagInput}
                onChangeText={setTagInput}
                onSubmitEditing={addTag}
              />
              <Button mode="contained" onPress={addTag} style={styles.addTagButton}>
                
              </Button>
            </View>
            <View style={styles.tagsContainer}>
              {tags.map((tag, index) => (
                <Chip
                  key={index}
                  style={styles.tagChip}
                  onClose={() => removeTag(index)}
                  onPress={() => removeTag(index)}
                >
                  {tag}
                </Chip>
              ))}
            </View>
            {errors.tags && (
              <HelperText type="error">{errors.tags.message}</HelperText>
            )}
          </View>

          <Divider style={styles.divider} />

          {/*   */}
          <View style={styles.formGroup}>
            <View style={styles.rowBetween}>
              <Text style={styles.label}> </Text>
              <Text style={styles.pointInfo}>
                : <Text style={{ color: colors.primary, fontWeight: 'bold' }}>{user?.points?.available || 0}P</Text>
              </Text>
            </View>
            <Controller
              control={control}
              name="points"
              render={({ field: { onChange, onBlur, value } }) => (
                <TextInput
                  style={styles.pointsInput}
                  placeholder="10"
                  value={value ? value.toString() : ''}
                  onChangeText={(text) => {
                    const numValue = text ? parseInt(text.replace(/[^0-9]/g, '')) : '';
                    onChange(numValue);
                  }}
                  onBlur={onBlur}
                  error={!!errors.points}
                  keyboardType="numeric"
                  right={<TextInput.Affix text="P" />}
                />
              )}
            />
            {errors.points && (
              <HelperText type="error">{errors.points.message}</HelperText>
            )}
            <Text style={styles.pointsDescription}>
                   .
            </Text>
          </View>

          {/*   */}
          <View style={styles.switchContainer}>
            <View style={styles.switchInfo}>
              <Ionicons name="eye-off-outline" size={20} color="#666" />
              <Text style={styles.switchLabel}> </Text>
            </View>
            <Switch
              value={isAnonymous}
              onValueChange={setIsAnonymous}
              color={colors.primary}
            />
          </View>

          {/*    */}
          <View style={styles.switchContainer}>
            <View style={styles.switchInfo}>
              <Ionicons name="time-outline" size={20} color="#666" />
              <Text style={styles.switchLabel}>  </Text>
            </View>
            <Switch
              value={isTimeLimit}
              onValueChange={setIsTimeLimit}
              color={colors.primary}
            />
          </View>

          {isTimeLimit && (
            <View style={styles.timeLimitContainer}>
              <Text style={styles.timeLimitLabel}>  :</Text>
              <View style={styles.timeLimitOptionsContainer}>
                {[6, 12, 24, 48, 72].map((hours) => (
                  <Chip
                    key={hours}
                    style={[
                      styles.timeLimitChip,
                      timeLimit === hours && { backgroundColor: colors.primary + '20' }
                    ]}
                    textStyle={timeLimit === hours ? { color: colors.primary } : {}}
                    onPress={() => setTimeLimit(hours)}
                    selected={timeLimit === hours}
                    mode="outlined"
                  >
                    {hours}
                  </Chip>
                ))}
              </View>
            </View>
          )}

          <Divider style={styles.divider} />

          {/*   */}
          <View style={styles.authorInfoContainer}>
            <View style={styles.rowBetween}>
              <Text style={styles.label}> </Text>
              {isAnonymous && (
                <Chip mode="outlined" style={styles.anonymousChip}>
                   
                </Chip>
              )}
            </View>
            <View style={styles.authorProfile}>
              <Avatar.Image size={40} source={{ uri: user?.profileImage || 'https://ui-avatars.com/api/?name=User' }} />
              <View style={styles.authorDetails}>
                <Text style={styles.authorName}>{isAnonymous ? '' : user?.name || ''}</Text>
                <Text style={styles.authorLevel}>Lv.{user?.level || 1} | {user?.expertise?.join(', ') || ' '}</Text>
              </View>
            </View>
          </View>

          {/*   */}
          <Button
            mode="contained"
            onPress={handleSubmit(onSubmit)}
            style={styles.submitButton}
            labelStyle={styles.submitButtonLabel}
            loading={loading}
            disabled={loading}
          >
             
          </Button>

          <View style={styles.bottomSpace} />
        </View>
      </ScrollView>
    </KeyboardAvoidingView>
  );
};

