import React, { useContext, useEffect, useState } from 'react';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { createStackNavigator } from '@react-navigation/stack';
import { Ionicons } from '@expo/vector-icons';
import { useTheme } from 'react-native-paper';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { View, Text } from 'react-native';

import AuthContext from '../context/AuthContext';

//  
import SignInScreen from '../screens/auth/SignInScreen';
import SignUpScreen from '../screens/auth/SignUpScreen';
// import ForgotPasswordScreen from '../screens/auth/ForgotPasswordScreen';
import OnboardingScreen from '../screens/auth/OnboardingScreen';

//  
import HomeScreen from '../screens/main/HomeScreen';
// import ExploreScreen from '../screens/main/ExploreScreen';
import AskScreen from '../screens/main/AskScreen';
// import NotificationScreen from '../screens/main/NotificationScreen';
// import ProfileScreen from '../screens/main/ProfileScreen';

//  
// import QuestionDetailScreen from '../screens/detail/QuestionDetailScreen';
// import ExperienceDetailScreen from '../screens/detail/ExperienceDetailScreen';
// import EbookDetailScreen from '../screens/detail/EbookDetailScreen';
// import UserProfileScreen from '../screens/detail/UserProfileScreen';
// import WriteExperienceScreen from '../screens/create/WriteExperienceScreen';
import WriteQuestionScreen from '../screens/create/WriteQuestionScreen';
// import WriteAnswerScreen from '../screens/create/WriteAnswerScreen';
// import PointHistoryScreen from '../screens/profile/PointHistoryScreen';
// import SettingsScreen from '../screens/profile/SettingsScreen';

const Tab = createBottomTabNavigator();
const Stack = createStackNavigator();
const AuthStack = createStackNavigator();
const HomeStack = createStackNavigator();
const ExploreStack = createStackNavigator();
const AskStack = createStackNavigator();
const NotificationStack = createStackNavigator();
const ProfileStack = createStackNavigator();

//   
const TempScreen = ({ route }) => (
  <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
    <Text> : {route.name}</Text>
  </View>
);

//    
const AuthStackScreen = () => {
  const [showOnboarding, setShowOnboarding] = useState(true);
  
  useEffect(() => {
    //    
    const checkOnboarding = async () => {
      try {
        const value = await AsyncStorage.getItem('@viewedOnboarding');
        
        if (value === 'true') {
          setShowOnboarding(false);
        }
      } catch (err) {
        console.log('Onboarding check error: ', err);
      }
    };
    
    checkOnboarding();
  }, []);

  return (
    <AuthStack.Navigator screenOptions={{ headerShown: false }}>
      {showOnboarding && (
        <AuthStack.Screen name="Onboarding" component={OnboardingScreen} />
      )}
      <AuthStack.Screen name="SignIn" component={SignInScreen} />
      <AuthStack.Screen name="SignUp" component={SignUpScreen} />
      <AuthStack.Screen name="ForgotPassword" component={TempScreen} />
    </AuthStack.Navigator>
  );
};

//   
const HomeStackScreen = () => (
  <HomeStack.Navigator>
    <HomeStack.Screen name="" component={HomeScreen} options={{ headerShown: false }} />
    <HomeStack.Screen name="QuestionDetail" component={TempScreen} options={{ title: ' ' }} />
    <HomeStack.Screen name="ExperienceDetail" component={TempScreen} options={{ title: ' ' }} />
    <HomeStack.Screen name="EbookDetail" component={TempScreen} options={{ title: ' ' }} />
    <HomeStack.Screen name="UserProfile" component={TempScreen} options={{ title: '' }} />
    <HomeStack.Screen name="WriteAnswer" component={TempScreen} options={{ title: ' ' }} />
  </HomeStack.Navigator>
);

//   
const ExploreStackScreen = () => (
  <ExploreStack.Navigator>
    <ExploreStack.Screen name="" component={TempScreen} options={{ headerShown: false }} />
    <ExploreStack.Screen name="QuestionDetail" component={TempScreen} options={{ title: ' ' }} />
    <ExploreStack.Screen name="ExperienceDetail" component={TempScreen} options={{ title: ' ' }} />
    <ExploreStack.Screen name="EbookDetail" component={TempScreen} options={{ title: ' ' }} />
    <ExploreStack.Screen name="UserProfile" component={TempScreen} options={{ title: '' }} />
  </ExploreStack.Navigator>
);

//   
const AskStackScreen = () => (
  <AskStack.Navigator>
    <AskStack.Screen name="" component={AskScreen} options={{ headerShown: false }} />
    <AskStack.Screen name="WriteQuestion" component={WriteQuestionScreen} options={{ headerShown: false }} />
    <AskStack.Screen name="WriteExperience" component={TempScreen} options={{ title: ' ' }} />
  </AskStack.Navigator>
);

//   
const NotificationStackScreen = () => (
  <NotificationStack.Navigator>
    <NotificationStack.Screen name="" component={TempScreen} options={{ headerShown: false }} />
  </NotificationStack.Navigator>
);

//   
const ProfileStackScreen = () => (
  <ProfileStack.Navigator>
    <ProfileStack.Screen name="" component={TempScreen} options={{ headerShown: false }} />
    <ProfileStack.Screen name="PointHistory" component={TempScreen} options={{ title: ' ' }} />
    <ProfileStack.Screen name="Settings" component={TempScreen} options={{ title: '' }} />
    <ProfileStack.Screen name="WriteExperience" component={TempScreen} options={{ title: ' ' }} />
  </ProfileStack.Navigator>
);

//   
const TabNavigator = () => {
  const theme = useTheme();
  
  return (
    <Tab.Navigator
      screenOptions={({ route }) => ({
        tabBarIcon: ({ focused, color, size }) => {
          let iconName;

          if (route.name === '') {
            iconName = focused ? 'home' : 'home-outline';
          } else if (route.name === '') {
            iconName = focused ? 'compass' : 'compass-outline';
          } else if (route.name === '') {
            iconName = focused ? 'help-circle' : 'help-circle-outline';
          } else if (route.name === '') {
            iconName = focused ? 'notifications' : 'notifications-outline';
          } else if (route.name === '') {
            iconName = focused ? 'person' : 'person-outline';
          }

          return <Ionicons name={iconName} size={size} color={color} />;
        },
        tabBarActiveTintColor: theme.colors.primary,
        tabBarInactiveTintColor: 'gray',
      })}
    >
      <Tab.Screen name="" component={HomeStackScreen} options={{ headerShown: false }} />
      <Tab.Screen name="" component={ExploreStackScreen} options={{ headerShown: false }} />
      <Tab.Screen name="" component={AskStackScreen} options={{ headerShown: false }} />
      <Tab.Screen name="" component={NotificationStackScreen} options={{ headerShown: false }} />
      <Tab.Screen name="" component={ProfileStackScreen} options={{ headerShown: false }} />
    </Tab.Navigator>
  );
};

//  
export default function Navigation() {
  const { token, loading } = useContext(AuthContext);

  if (loading) {
    return null; //       (   )
  }

  return (
    <Stack.Navigator screenOptions={{ headerShown: false }}>
      {token ? (
        <Stack.Screen name="Main" component={TabNavigator} />
      ) : (
        <Stack.Screen name="Auth" component={AuthStackScreen} />
      )}
    </Stack.Navigator>
  );
} 